// Dependencies
import { FC } from 'react';
import { Stack, Table, TableBody, TableCell, TableRow } from '@mui/material';

// Mock data
const data1 = [
  'Tagesaktuelles Factsheet	de_DE',
  'Monatsultimo Factsheet	de_DE',
  'KIID	de_DE en_GB',
  'Verkaufsprospekt	1356570',
  'Jahresbericht	64203 64201',
  'Halbjahresbericht	26044 26045',
  'Marktkommentar',
];

const data2 = [
  { a: 'Mitteilung an die Anleger', b: '10.06.2022' },
  { a: 'Mitteilung an die Anleger', b: '01.04.2022' },
  { a: 'Selbstauskunft von Gesellschaften', b: '07.01.2022' },
  { a: 'Zeichnungsschein - juristische Personen', b: '07.01.2022' },
  { a: 'Zeichnungsschein - minderjährige Personen', b: '07.01.2022' },
  { a: 'Zeichnungsschein - natürliche Personen', b: '07.01.2022' },
  { a: 'Dealing Calendar 2022', b: '03.01.2022' },
  { a: 'Mitteilung an die Anleger', b: '09.12.2021' },
  { a: 'Mitteilung an die Anleger', b: '21.10.2021' },
  { a: 'Mitteilung an die Anleger', b: '06.09.2021' },
];

// Export document panel
export const DocumentPanel: FC = () => {
  return (
    <Stack spacing={30}>
      <Table>
        <TableBody>
          {data1.map((value, index) => (
            <TableRow key={index}>
              <TableCell>{value}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <Table>
        <TableBody>
          {data2.map(({ a, b }, index) => (
            <TableRow key={index}>
              <TableCell>{a}</TableCell>
              <TableCell>{b}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </Stack>
  );
};
