import React, { FC, useState } from 'react';
import { useQuery } from '@apollo/client';
import ApexCharts from 'apexcharts';
import ReactApexChart from 'react-apexcharts';
import moment from 'moment';

import { GET_HISTORIES } from '../../../queries/history';

export const PerformancePage: FC = () => {
  const [options] = useState<any>({
    chart: {
      id: 'area-datetime',
      type: 'area',
      height: 350,
      zoom: {
        autoScaleYaxis: true,
      },
    },
    annotations: {
      yaxis: [
        {
          y: 30,
          borderColor: '#999',
          label: {
            show: true,
            text: 'Support',
            style: {
              color: '#fff',
              background: '#00E396',
            },
          },
        },
      ],
      xaxis: [
        {
          borderColor: '#999',
          yAxisIndex: 0,
          label: {
            show: true,
            text: 'Rally',
            style: {
              color: '#fff',
              background: '#775DD0',
            },
          },
        },
      ],
    },
    dataLabels: {
      enabled: false,
    },
    markers: {
      size: 0,
      style: 'hollow',
    },
    xaxis: {
      type: 'datetime',
      tickAmount: 6,
    },
    tooltip: {
      x: {
        format: 'MMM dd yyyy',
      },
    },
    fill: {
      type: 'gradient',
      gradient: {
        shadeIntensity: 1,
        opacityFrom: 0.7,
        opacityTo: 0.9,
        stops: [0, 100],
      },
    },
  });
  const [type, setType] = useState('today');

  const { data: { getHistory: data = [] } = {} } = useQuery(GET_HISTORIES, {
    fetchPolicy: 'network-only',
    notifyOnNetworkStatusChange: true,
    variables: type,
  });

  const updateData = (timeline: any) => {
    switch (timeline) {
      case 'today':
        ApexCharts.exec('area-datetime', 'zoomX', +new Date() - 24 * 60 * 60 * 1000, +new Date());
        break;
      case 'one_month':
        ApexCharts.exec('area-datetime', 'zoomX', +new Date() - 30 * 24 * 60 * 60 * 1000, +new Date());
        break;
      case 'six_months':
        ApexCharts.exec('area-datetime', 'zoomX', moment().subtract(6, 'months').valueOf().toString(), +new Date());
        break;
      case 'one_year':
        ApexCharts.exec('area-datetime', 'zoomX', moment().subtract(1, 'years').valueOf().toString(), +new Date());
        break;
      case 'ytd':
        ApexCharts.exec('area-datetime', 'zoomX', moment().startOf('year').valueOf().toString(), +new Date());
        break;
      case 'all':
        ApexCharts.exec(
          'area-datetime',
          'zoomX',
          moment().subtract(5, 'years').valueOf().toString(),
          moment().valueOf().toString()
        );
        break;
      default:
    }

    const now = moment().valueOf().toString();
    const dates: any = {
      today: moment().subtract(1, 'days').valueOf().toString(),
      one_month: moment().subtract(1, 'months').valueOf().toString(),
      six_months: moment().subtract(6, 'months').valueOf().toString(),
      one_year: moment().subtract(1, 'years').valueOf().toString(),
      ytd: moment().startOf('year').valueOf().toString(),
      all: moment().valueOf().toString(),
    };
    //
    // if (timeline === 'today') {
    //   setData(dataForToday);
    // } else if (timeline === 'all') {
    //   setData(dataForAll);
    // } else {
    //   const data = dataForAll.filter(d => d[0] >= dates[timeline] && d[0] <= now);
    //   setData(data);
    // }

    setType(timeline);
  };

  return (
    <div id="chart">
      <div className="toolbar">
        <button id="today" onClick={() => updateData('today')} className={type === 'today' ? 'active' : 'non-active'}>
          Today
        </button>
        &nbsp;
        <button
          id="one_month"
          onClick={() => updateData('one_month')}
          className={type === 'one_month' ? 'active' : 'non-active'}
        >
          1 Month
        </button>
        &nbsp;
        <button
          id="six_months"
          onClick={() => updateData('six_months')}
          className={type === 'six_months' ? 'active' : 'non-active'}
        >
          6 Month
        </button>
        &nbsp;
        <button
          id="one_year"
          onClick={() => updateData('one_year')}
          className={type === 'one_year' ? 'active' : 'non-active'}
        >
          1 Year
        </button>
        &nbsp;
        <button id="ytd" onClick={() => updateData('ytd')} className={type === 'ytd' ? 'active' : 'non-active'}>
          YtD
        </button>
        &nbsp;
        <button id="all" onClick={() => updateData('all')} className={type === 'all' ? 'active' : 'non-active'}>
          All
        </button>
      </div>

      <div id="chart-timeline">
        <ReactApexChart
          options={options}
          series={[
            {
              name: 'Frankfurter Modern value index',
              data: data,
            },
          ]}
          type="area"
          height={350}
        />
      </div>
    </div>
  );
};
