// Dependencies
import { FC } from 'react';
import { MenuItem } from '@mui/material';

// Menu list
const menuList = [
  'SEKTORENVERTEILUNG',
  'WÄHRUNG',
  'TOP POSITIONEN',
  'SRRI (SYNTHETIC RISK AND REWARD INDICATOR)',
  'ISIKOKENNZAHLEN (5 JAHRE)',
];

// Export portfolio panel
export const PortfolioPanel: FC = () => {
  return (
    <>
      {menuList.map((menu, index) => (
        <MenuItem key={index}>{menu}</MenuItem>
      ))}
    </>
  );
};
