// Dependencies
import { FC } from 'react';
import { MenuItem } from '@mui/material';

// Menu list
const menuList = [
  'ESG-KENNZAHLEN ZUM GESCHÄFTSENGAGEMENT',
  'ESG-RISIKO RATINGS VERTEILUNG',
  'ESG-RISIKO SCORE & COVERAGE',
  'ESG-DOKUMENTE',
  'ESG-RATING',
];

// Export esg panel
export const ESGPanel: FC = () => {
  return (
    <>
      {menuList.map((menu, index) => (
        <MenuItem key={index}>{menu}</MenuItem>
      ))}
    </>
  );
};
