// Dependencies
import { FC } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';

// Routes
import { MAIN_ROUTES, ROUTES } from '../contants';

// Create app router
const AppRouter: FC = () => (
  <Router>
    <Routes>
      {MAIN_ROUTES.map(({ path, component: C }) => (
        <Route key={path} path={path} element={<C />} />
      ))}
      <Route path="*" key={''} element={<Navigate to={ROUTES.PRODUCT.CHART.PERFORMANCE} />} />
    </Routes>
  </Router>
);

// Export app router
export default AppRouter;
