// Dependencies
import { FC } from 'react';
import { MenuItem } from '@mui/material';

// Menu list
const menuList = [
  'BRUTTO-WERTENTWICKLUNG (IN %)',
  'JÄHRLICHE BRUTTO-WERTENTWICKLUNG (IN %)',
  'KUMULIERTE BRUTTO-WERTENTWICKLUNG (IN %)',
  'ANNUALISIERTE BRUTTO-WERTENTWICKLUNG (IN %)',
];

// Export wicklung panel
export const WicklungPanel: FC = () => {
  return (
    <>
      {menuList.map((menu, index) => (
        <MenuItem key={index}>{menu}</MenuItem>
      ))}
    </>
  );
};
