// Dependencies
import React, { FC, useState } from 'react';
import { Tab, Tabs } from '@mui/material';
import { Link, useParams } from 'react-router-dom';

// Global constants
import { ROUTES } from '../../contants';

// Panels
import { DocumentPanel, ESGPanel, PortfolioPanel, PortraitPanel, WicklungPanel } from './panels';

// Interfaces
interface ITab {
  label: string;
  id: string | number;
  panel?: FC;
}

// Tabs
const navTabs: ITab[] = [
  {
    label: 'TRANCHE',
    id: 'a',
  },
  {
    label: 'A',
    id: 'a',
  },
  {
    label: 'B',
    id: 'b',
  },
  {
    label: 'C',
    id: 'c',
  },
  {
    label: 'D',
    id: 'd',
  },
  {
    label: 'R',
    id: 'r',
  },
  {
    label: 'X',
    id: 'x',
  },
];

const tabs: ITab[] = [
  {
    label: 'PORTRAIT',
    id: 1,
    panel: PortraitPanel,
  },
  {
    label: 'WERTENTWICKLUNG',
    id: 2,
    panel: WicklungPanel,
  },
  {
    label: 'PORTFOLIO',
    id: 3,
    panel: PortfolioPanel,
  },
  {
    label: 'ESG',
    id: 4,
    panel: ESGPanel,
  },
  {
    label: 'DOKUMENTE',
    id: 5,
    panel: DocumentPanel,
  },
];

// Export fact-sheet age
export const FactSheetPage: FC = () => {
  // States
  const [activeTabId, setActiveTabId] = useState<number | string>(tabs[0].id);

  // Get active nav id from hook
  const { id: activeNavId } = useParams<{ id: string }>();

  // Tab change handler
  const handleTabChange = (_: React.SyntheticEvent, activeId: number) => {
    setActiveTabId(activeId);
  };

  // Export fact-sheet page
  return (
    <>
      <Tabs value={activeNavId}>
        {navTabs.map(({ label, id }, index) => (
          <Tab
            key={index}
            value={id}
            label={label}
            component={Link as React.ElementType}
            to={`${ROUTES.PRODUCT.PREFIX}/${id}/fact-sheet`}
          />
        ))}
      </Tabs>
      <Tabs value={activeTabId} onChange={handleTabChange}>
        {tabs.map(({ label, id }, index) => (
          <Tab key={index} value={id} label={label} />
        ))}
      </Tabs>
      {tabs.map(({ id, panel: P }) => (
        <React.Fragment key={id}>{activeTabId === id && P && <P />}</React.Fragment>
      ))}
    </>
  );
};
