// Dependencies
import React from 'react';
import { Components } from '@mui/material';

// Colors
import { gray, green, grey, white } from './palette';

// Override components
const components: Components = {
  MuiTabs: {
    styleOverrides: {
      root: {
        borderBottom: `1px solid ${green}`,
        marginBottom: 40,
      },
      indicator: {
        display: 'none',
      },
    },
  },
  MuiTab: {
    styleOverrides: {
      root: {
        minWidth: 'unset',
        padding: '10px 20px',
        backgroundColor: grey,
        '&.Mui-selected, &:hover': {
          backgroundColor: green,
          color: white,
        },
        '& + &': {
          marginLeft: 2,
        },
      },
    },
  },
  MuiTableRow: {
    styleOverrides: {
      root: {
        '&:nth-of-type(even)': {
          backgroundColor: grey,
        },
      },
    },
  },
  MuiTableCell: {
    styleOverrides: {
      root: {
        padding: '5px 5px 4px 5px',
      },
    },
  },
  MuiListItem: {
    styleOverrides: {
      root: {
        borderBottom: `1px dotted ${gray}`,
      },
    },
  },
  MuiMenuItem: {
    styleOverrides: {
      root: {
        fontSize: 20,
        fontWeight: 500,
        padding: '10px 0 5px 0',
        color: green,
        borderBottom: `1px solid ${green}`,
      },
    },
  },
};

// Export components
export default components;
